___CSS_LOADER_EXPORT___.push([module.id, `.findoc-widget {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
    max-width: 600px;
    margin: 0 auto;
    padding: 24px;
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.findoc-header {
    text-align: center;
    margin-bottom: 24px;
}

.findoc-icon {
    display: inline-block;
    color: #3b82f6;
    margin-bottom: 16px;
}

.findoc-header h2 {
    margin: 0 0 8px 0;
    font-size: 24px;
    font-weight: 600;
    color: #1f2937;
}

.findoc-header p {
    margin: 0;
    color: #6b7280;
    font-size: 14px;
}

.findoc-upload-area {
    border: 2px dashed #d1d5db;
    border-radius: 8px;
    padding: 40px 20px;
    text-align: center;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 20px;
    background: #f9fafb;
}

    .findoc-upload-area:hover {
        border-color: #3b82f6;
        background: #eff6ff;
    }

    .findoc-upload-area.dragover {
        border-color: #3b82f6;
        background: #dbeafe;
        transform: scale(1.02);
    }

.upload-label {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
}

.upload-icon {
    font-size: 48px;
    opacity: 0.6;
}

.upload-text {
    text-align: center;
}

    .upload-text p {
        margin: 0 0 4px 0;
        font-size: 16px;
    }

    .upload-text strong {
        color: #1f2937;
        font-weight: 600;
    }

    .upload-text small {
        color: #9ca3af;
        font-size: 12px;
    }

.findoc-files-list {
    margin-bottom: 20px;
}

    .findoc-files-list h3 {
        font-size: 16px;
        font-weight: 600;
        margin: 0 0 12px 0;
        color: #1f2937;
    }

#files-container {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.file-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px;
    background: #f9fafb;
    border-radius: 6px;
    border: 1px solid #e5e7eb;
}

.file-info {
    display: flex;
    align-items: center;
    gap: 8px;
    flex: 1;
    min-width: 0;
}

.file-icon {
    font-size: 20px;
    flex-shrink: 0;
}

.file-name {
    font-size: 14px;
    color: #1f2937;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.file-status {
    display: flex;
    align-items: center;
    gap: 8px;
    flex-shrink: 0;
}

.progress-bar {
    width: 100px;
    height: 6px;
    background: #e5e7eb;
    border-radius: 3px;
    overflow: hidden;
}

.progress-fill {
    height: 100%;
    background: linear-gradient(90deg, #3b82f6, #2563eb);
    transition: width 0.3s ease;
    border-radius: 3px;
}

.status-icon {
    font-size: 18px;
}

.status-text {
    font-size: 13px;
    color: #6b7280;
    white-space: nowrap;
}

.findoc-actions {
    display: flex;
    gap: 12px;
    justify-content: flex-end;
}

.btn-primary,
.btn-secondary {
    padding: 10px 20px;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    border: none;
    transition: all 0.2s ease;
    font-family: inherit;
}

.btn-primary {
    background: #3b82f6;
    color: white;
}

    .btn-primary:hover:not(:disabled) {
        background: #2563eb;
        transform: translateY(-1px);
        box-shadow: 0 4px 6px -1px rgba(59, 130, 246, 0.3);
    }

    .btn-primary:active:not(:disabled) {
        transform: translateY(0);
    }

    .btn-primary:disabled {
        background: #d1d5db;
        cursor: not-allowed;
        opacity: 0.6;
    }

.btn-secondary {
    background: #f3f4f6;
    color: #374151;
    border: 1px solid #e5e7eb;
}

    .btn-secondary:hover {
        background: #e5e7eb;
    }

    .btn-secondary:active {
        background: #d1d5db;
    }

@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.file-item {
    animation: slideIn 0.3s ease;
}

@media (max-width: 640px) {
    .findoc-widget {
        padding: 16px;
    }

    .findoc-header h2 {
        font-size: 20px;
    }

    .findoc-upload-area {
        padding: 30px 15px;
    }

    .progress-bar {
        width: 60px;
    }

    .findoc-actions {
        flex-direction: column;
    }

    .btn-primary,
    .btn-secondary {
        width: 100%;
    }
}
